.hero {
  position: relative;
  overflow-x: hidden;
  padding: calc(var(--header-height) + rem(150)) 0 rem(180);
  height: var(--vh);
  color: var(--color-white);
  background: var(--color-linear-600);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: 0;
    width: 75%;
    height: 100%;
    background-color: var(--color-black);
    opacity: 0.05;
  }

  &__inner {
    @include flex-all-center;

    @include for-width(-tablet) {
      flex-direction: column-reverse;
      margin: 0 auto;
      max-width: 550px;
    }
  }

  &__image {
    margin-right: rem(100);
    width: 100%;

    @include for-width(-tablet) {
      margin: 0 auto;
    }
  }

  &__content {
    width: 100%;

    .btn {
      margin-bottom: 0;
    }

    li {
      position: relative;
      padding-left: 20px;
      font-weight: 500;
      font-size: 15px;
      line-height: 1.73;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        border-radius: 50%;
        width: 7px;
        height: 7px;
        background-color: var(--color-white);
        transform: translateY(-50%);
      }

      &:not(:last-child) {
        margin-bottom: 14px;
      }
    }

    @include for-width(-tablet) {
      margin: 0 auto 30px;
    }
  }

  &__title {
    position: relative;
    margin-bottom: rem(81);
    font-weight: 700;
    font-size: 38px;
    line-height: 1.17;

    &::before {
      content: "";
      position: absolute;
      left: 2px;
      bottom: rem(-36);
      width: 100vw;
      height: 1px;
      background-color: var(--color-white);
    }

    @include for-width(-mobile-l) {
      font-size: 40px;
    }

    @include for-width(-mobile-sl) {
      font-size: 34px;
    }

    @include for-width(-mobile-sm) {
      font-size: 28px;
    }
  }

  &__text {
    margin-bottom: rem(40);
    max-width: 676px;
    font-weight: 400;
    font-size: 15px;
    color: var(--color-ligth-200);

    @include for-width(-mobile-sl) {
      font-size: 16px;
    }
  }

  &__btn {
    margin-bottom: rem(133);

    @include for-width(-mobile-l) {
      margin-bottom: rem(80);
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    max-width: 745px;
    gap: rem(80);

    span {
      position: relative;
      display: block;
      margin-bottom: rem(20);
      width: fit-content;
      font-weight: 700;
      font-size: 20px;
      line-height: 2;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -6px;
        width: calc(100% + 25px);
        height: 1px;
        background-color: var(--color-white);
      }
    }

    p {
      font-size: 12px;
      line-height: 1.67;
      color: var(--color-ligth-200);

      @include for-width(-mobile-sl) {
        font-size: 14px;
      }
    }

    @include for-width(-mobile-l) {
      gap: rem(40);
    }

    @include for-width(-mobile-md) {
      grid-template-columns: 1fr;
    }
  }

  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;

    @include for-width(-mobile-md) {
      display: block;
      height: auto;
    }
  }

  >* {
    position: relative;
    z-index: 1;
  }

  &--home {
    padding: rem(199) 0 rem(103);
    height: var(--vh);

    .container {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        right: 0;
        top: 50%;
        z-index: -1;
        width: 100%;
        height: 550px;
        max-width: 740px;
        background-image: url("../img/animation/hero.svg");
        background-position: center;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        transform: translateY(-50%);

        @include for-width(-laptop-mac) {
          height: 420px;
          max-width: 570px;
          transform: translateY(-40%);
        }

        @include for-width(-tablet) {
          display: none;
        }
      }
    }

    .hero {
      &__title {
        margin-top: auto;
        font-size: 46px;

        &::first-letter {
          padding-right: 5px;
          font-size: 115px;
          color: transparent;
          background-image: var(--color-linear-400);
          background-size: cover;
          background-clip: text;
          opacity: 0.85;

          @include for-width(-tablet) {
            padding: 0;
            font-size: 85px;
            line-height: 1;
          }

          @include for-width(-mobile-sl) {
            font-size: 70px;
          }

          @include for-width(-mobile-sm) {
            font-size: 60px;
          }
        }
      }

      &__text {
        margin-top: auto;
        margin-bottom: rem(30);
      }

      &__list {
        margin-top: auto;
      }
    }
  }

  &--liquidity {
    .hero {
      &__inner {
        transform: translateY(-20px);

        @include for-width(-tablet) {
          transform: translateY(0);
        }

        @include for-width(-mobile-l) {
          flex-direction: column-reverse;
        }
      }

      &__content {
        margin-top: rem(-30);

        @include for-width(-tablet) {
          margin-top: 0;
        }
      }
    }
  }

  &--solution {
    .hero {
      &__inner {
        transform: translateY(-12px);

        @include for-width(-tablet) {
          transform: translateY(0);
        }
      }

      &__content {
        padding-top: 15px;
      }
    }
  }

  &--404 {
    height: 100vh;

    &::after {
      @include for-width(-mobile-md) {
        display: block;
      }
    }

    .hero {
      &__text {
        margin-top: initial;
      }

      &__title {
        margin-top: initial;
      }

      &__btn {
        margin-top: initial;
      }
    }

    .container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;

      &::after {
        display: block;
      }
    }

    &.hero {
      @include for-width(-mobile-md) {
        height: 100vh;
      }
    }
  }

  @include for-width(-laptop-mac) {
    padding: calc(var(--header-height) + rem(100)) 0 rem(130);
  }

  @include for-width(-mobile-l) {
    padding: calc(var(--header-height) + rem(50)) 0 rem(100);
  }

  @include for-width(-mobile-md) {
    height: auto;
  }
}

@media (width <=1080px) and (orientation: landscape) {
  .hero {
    padding: var(--header-height) 0 rem(100);
    height: auto;

    .container {
      display: block;
      height: auto;
    }

    &--404 {
      height: 100vh;

      .container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;

        &::after {
          display: block;
        }
      }
    }
  }
}
