dialog.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  overflow: auto;
  border: none;
  padding: 0;
  width: 100vw;
  height: 100%;
  background-color: transparent;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: var(--color-black);
    opacity: 0.2;
  }

  &__inner {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 1;
    overflow: hidden;
    border-radius: 3px 3px 10px 10px;
    width: 100%;
    max-width: 655px;
    background-color: var(--color-white);
    transform: translate(-50%, -50%);
  }

  &__content {
    padding: rem(40) rem(20);
  }

  &__image {
    padding: rem(60);
    text-align: center;
    background-color: var(--color-purple-400);
  }

  &__title {
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 25px;
    line-height: 1.2;
    text-align: center;
  }

  p {
    margin: 0 auto 20px;
    width: 100%;
    max-width: 310px;
    font-weight: 400;
    font-size: 13px;
    line-height: 1.8;
    text-align: center;
  }

  &__btn {
    margin: 0 auto;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
  }
}
