.carrer {
  padding: rem(100) 0;
  background-color: var(--color-ligth-200);

  &__item {
    @include flex-sb;

    position: relative;
    align-items: flex-start;
    padding: rem(46) rem(47) rem(34);
    background-color: var(--color-white);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 5px;
      height: 100%;
      background-color: var(--color-purple-300);
    }

    &:not(:last-child) {
      margin-bottom: rem(30);
    }

    @include for-width(-mobile-l) {
      flex-direction: column;
      padding: rem(30) rem(20);
    }
  }

  &__common {
    margin-right: 20px;

    span {
      position: relative;
      display: block;
      padding-left: 22px;
      font-weight: 400;
      font-size: 17px;
      line-height: 1.76;

      &:first-of-type {
        &::before {
          content: "";
          position: absolute;
          left: -2px;
          top: 3px;
          mask-image: url("../img/sprite.svg#pin");
          mask-position: center;
          mask-repeat: no-repeat;
          mask-size: contain;
          width: 18px;
          height: 18px;
          background-color: var(--color-gray-300);
        }
      }

      @include for-width(-mobile-l) {
        display: inline-block;
      }
    }

    @include for-width(-mobile-l) {
      margin-right: 0;
      margin-bottom: 30px;
    }
  }

  &__title {
    display: flex;
    align-items: center;
    margin-bottom: 6px;
    width: 100%;
    min-width: 275px;
    min-height: 60px;
    max-width: 400px;
    font-weight: 500;
    font-size: 26px;
    line-height: 1.54;

    @include for-width(-mobile-l) {
      min-width: auto;
      max-width: none;
      font-size: 24px;
    }
  }

  &__desc {
    width: 100%;
    max-width: 853px;

    p {
      margin-bottom: 13px;
      min-height: 80px;
    }

    a {
      color: var(--color-purple-300);
    }
  }
}
