.oportunities {
  &__title {
    margin: 0 auto rem(50);
    width: fit-content;
    font-weight: 700;

    &::before {
      right: 0;
      bottom: rem(-25);
      margin: 0 auto;
      width: 76%;
      background-color: var(--color-dark-400);
    }
  }

  &__text {
    margin: 0 auto rem(65);
    max-width: 553px;
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr) 0.65fr;
    margin-bottom: 5px;
    gap: rem(68);

    @include for-width(-laptop) {
      gap: rem(40);
    }

    @include for-width(-laptop-mac) {
      grid-template-columns: repeat(4, 1fr) 0.5fr;
    }

    @include for-width(-tablet) {
      grid-template-columns: repeat(2, 1fr);
      margin: 0 auto;
      width: 85%;
      text-align: center;
      gap: rem(20);
    }

    @include for-width(-mobile-l) {
      width: 100%;
    }

    @include for-width(-mobile-sl) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    position: relative;
    padding-top: rem(90);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border-radius: var(--radius-main);
      width: 60px;
      height: 60px;
      background-color: var(--color-ligth-150);

      @include for-width(-tablet) {
        right: 0;
        margin: 0 auto;
      }
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: cover;
      width: 25px;
      height: 25px;
      background-color: var(--color-purple-400);
      transform: translate(65%, 65%);

      @include for-width(-tablet) {
        right: 0;
        margin: 0 auto;
        transform: translate(0, 65%);
      }
    }

    span {
      display: block;
      margin-bottom: rem(17);
      font-weight: 700;
      font-size: 16px;
      line-height: 1.18;
      color: var(--color-dark-400);
    }

    &:last-child {
      @include for-width(-tablet) {
        grid-column: 1 / span 2;
      }

      @include for-width(-mobile-sl) {
        grid-column: auto;
      }
    }

    &--stock {
      &::after {
        mask-image: url('../img/sprite.svg#graph');
      }
    }

    &--options {
      &::after {
        mask-image: url('../img/sprite.svg#line-graph');
      }
    }

    &--etfs {
      &::after {
        mask-image: url('../img/sprite.svg#pie');
      }
    }

    &--otcs {
      &::after {
        mask-image: url('../img/sprite.svg#circle');
        width: 27px;
        height: 17px;
        transform: translate(60%, 115%);

        @include for-width(-tablet) {
          transform: translate(0, 115%);
        }
      }
    }

    &--adrs {
      &::after {
        mask-image: url('../img/sprite.svg#adr');
        width: 28px;
        transform: translate(55%, 65%);

        @include for-width(-tablet) {
          transform: translate(0, 65%);
        }
      }
    }
  }
}
