.title {
  position: relative;
  margin-bottom: rem(48);
  font-weight: 500;
  font-size: 26px;
  line-height: 1.7;
  text-align: center;
  color: var(--color-dark-400);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: rem(-20);
    width: 100%;
    height: 1px;
    background-color: var(--color-purple-200);
  }

  &--colored {
    &::first-letter {
      font-weight: 900;
      font-size: 40px;
      text-transform: capitalize;
      color: transparent;
      background-image: var(--color-linear-400);
      background-size: cover;
      background-clip: text;
      opacity: 0.85;

      @include for-width(-mobile-md) {
        font-size: 37px;
      }
    }
  }

  @include for-width(-mobile-md) {
    font-size: 24px;
  }
}

.subtitle {
  margin-bottom: 11px;
  font-weight: 700;
  font-size: 17px;
  line-height: 1.17;
}

.text {
  width: 100%;
}
