.footer {
  padding: rem(83) 0;
  text-align: center;

  &__list {
    @include flex-all-center;

    margin-bottom: rem(42);

    @include for-width(-mobile-md) {
      flex-direction: column;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: rem(37);

      @include for-width(-mobile-md) {
        margin-right: 0;
        margin-bottom: 25px;
      }
    }
  }

  &__terms {
    @include flex-all-center;

    font-weight: 400;
    font-size: 13px;
    line-height: 1.55;
    color: var(--color-gray-300);
  }

  &__term {
    position: relative;

    &:not(:last-child) {
      margin-right: rem(70);

      &::before {
        content: "";
        position: absolute;
        right: rem(-36);
        top: 3px;
        width: 1px;
        height: 70%;
        background-color: var(--color-gray-300);

        @include for-width(-mobile-md) {
          right: rem(-16);
        }
      }

      @include for-width(-mobile-md) {
        margin-right: rem(30);
      }
    }
  }

  .logo {
    margin: 0 auto rem(29);
    max-width: 126px;
  }

  &--border {
    border-top: 1px solid var(--color-gray-300);
  }

  @include for-width(-laptop-mac) {
    padding: rem(60) 0;
  }
}
