.section {
  position: relative;
  padding: rem(154) 0;

  &__inner {
    @include flex-sb;

    align-items: center;

    @include for-width(-tablet) {
      flex-direction: column-reverse;
    }

    &--reverse {
      flex-direction: row-reverse;

      @include for-width(-tablet) {
        flex-direction: column-reverse;
      }
    }

    &--size {
      .section {
        &__content {
          padding-top: rem(67);

          p {
            margin-bottom: rem(40);
          }
        }

        &__image {
          transform: translate(48px, 4px);
        }
      }
    }

    &--dividen {
      align-items: flex-start;

      .section {
        &__image {
          margin-right: 0;
          transform: translate(0, 6px);
        }

        &__content {
          padding-top: rem(27);
        }
      }
    }

    &--quantitative {
      .section {
        &__image {
          transform: translate(45px, 4px);
        }

        &__content {
          margin-top: rem(-76);
        }
      }
    }

    &--oval {
      .section {
        &__image {
          margin-right: 0;
          transform: translate(0, 4px);
        }
      }
    }

    &--dark {
      .section {
        &__image {
          margin-right: 0;
          transform: translate(0, 6px);
        }

        &__content {
          margin-top: -16px;
        }
      }
    }

    &--api {
      span {
        display: inline-block;
        margin-right: 5px;
        font-weight: 600;
        font-size: 40px;
        line-height: 1.7;
        text-align: center;
        text-transform: uppercase;
        color: transparent;
        background-image: var(--color-linear-400);
        background-size: cover;
        background-clip: text;
        opacity: 0.85;
      }

      .section {
        &__inner {
          align-items: flex-start;
        }

        &__content {
          margin-top: rem(-22);
        }

        &__image {
          margin-right: 0;
          transform: translate(0, 6px);
        }
      }

      .title {
        &::first-letter {
          color: inherit;
        }
      }
    }
  }

  &__content {
    padding-top: 7px;
    width: 100%;
    max-width: 600px;

    p {
      &:not(:last-child) {
        margin-bottom: rem(30);
      }
    }

    >* {
      text-align: left;
    }

    &--multitext {
      align-self: flex-start;

      p {
        &:first-of-type {
          margin-bottom: rem(88);
        }
      }
    }

    &--up {
      margin-top: rem(-68);
    }

    &--word {
      span {
        display: inline-block;
        margin-right: 5px;
        font-weight: 600;
        font-size: 40px;
        line-height: 1.7;
        text-align: center;
        text-transform: uppercase;
        color: transparent;
        background-image: var(--color-linear-400);
        background-size: cover;
        background-clip: text;
        opacity: 0.85;
      }

      .section {
        &__inner {
          align-items: flex-start;
        }

        &__content {
          padding-top: rem(74);
        }
      }

      .title {
        &::first-letter {
          color: inherit;
        }
      }
    }

    &--interface {
      margin-top: rem(-95);
    }

    @include for-width(-laptop) {
      margin-top: 0 !important;
      padding-top: 0 !important;
    }

    @include for-width(-laptop) {
      margin: 0 auto rem(40);
    }

    @include for-width(-tablet) {
      max-width: 760px;
    }
  }

  &__image {
    margin: 0 auto;
    width: 100%;
    transform: translate(32px, 4px);

    @include for-width(-laptop) {
      transform: translate(0, 0) !important;
    }

    @include for-width(-tablet) {
      margin: 0 auto !important;
      max-width: 620px;
    }
  }

  &--colored {
    padding: rem(175) 0 rem(107);

    .section {
      &__image {
        margin-left: 0;
        transform: translate(31px, -20px);
      }
    }
  }

  &--multiply {
    align-items: flex-start;

    .section {
      &__image {
        transform: translate(0, -30px);
      }

      &__content {
        margin-top: rem(-95);
      }
    }
  }

  &--light {
    .section {
      &__image {
        transform: translate(-80px, -55px);
      }

      &__content {
        align-self: flex-start;
        margin-top: rem(70);
      }
    }
  }

  &--home {
    .section {
      &__image {
        transform: translate(40px, -10px);
        max-width: 660px;
      }
    }
  }

  &--minimize {
    padding: rem(97) 0 rem(121);

    .section {
      &__image {
        max-width: 600px;
        transform: translate(50px, 26px);
      }
    }
  }

  &--about {
    padding: rem(97) 0 rem(150);

    .section {
      &__image {
        transform: translate(50px, 0);
      }
    }
  }

  &--historical {
    .section {
      &__image {
        transform: translate(0, -28px);
      }

      &__content {
        margin-top: rem(-85);
      }
    }
  }

  @include for-width(-laptop-mac) {
    padding: rem(100) 0;
  }

  @include for-width(-tablet) {
    padding: rem(70) 0 rem(40);
  }
}
