.cookie {
  position: fixed;
  bottom: 0;
  z-index: 1;
  width: 100%;
  background-color: var(--color-white);

  &__inner {
    @include flex;

    align-items: flex-end;
    padding: 30px 0;

    @include for-width(-mobile-l) {
      flex-direction: column;
    }
  }

  &__agree {
    margin-right: 30px;
    width: 100%;
    max-width: 1115px;

    @include for-width(-mobile-l) {
      margin-right: 0;
      margin-bottom: 30px;
      max-width: none;
    }
  }

  &__btns {
    --width: 163px;

    margin-left: auto;
    width: 100%;
    max-width: var(--width);

    .btn {
      padding: 11px 10px 12px;
      font-size: 14px;

      &:first-of-type {
        margin-bottom: 20px;

        @include for-width(-mobile-l) {
          margin-right: 30px;
          margin-bottom: 0;
        }
      }

      &:last-of-type {
        font-weight: 500;

        @include for-width(-mobile-l) {
          margin-top: 0;
        }
      }
    }

    @include for-width(-mobile-l) {
      display: flex;
      max-width: calc(var(--width) * 2);
    }
  }

  h3 {
    margin-bottom: 10px;
    font-weight: 700;
    font-size: 17px;
    line-height: 1.76;
  }

  p {
    font-weight: 400;
    font-size: 12px;
    line-height: 1.83;
  }
}
