.page {
  font-family: var(--font-family);
  font-weight: 400;
  font-style: normal;
  font-feature-settings: "pnum"on, "lnum"on;
  font-size: 15px;
  line-height: 1.73;
  -webkit-text-size-adjust: 100%;
  color: var(--color-dark-400);
  background-color: var(--color-white);
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
  scrollbar-width: none;

  &__body {
    margin: 0;
    min-width: 320px;
    min-height: 100%;
  }
}

.site-container {
  @include footerToBottom;
}

.main {
  position: relative;
}

.hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.sr-only {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.logo {
  @include flex-all-center;

  width: 100%;
  max-width: 85px;
  transform: translateY(-3px);
}

.section-background {
  position: relative;
  clip-path: inset(0 -100vmax);
  box-shadow: 0 0 0 100vmax var(--color-ligth-200);
  background-color: var(--color-ligth-200);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    clip-path: inset(0 -17% 0 -100vmax);
    width: 20%;
    height: 100%;
    min-width: 325px;
    box-shadow: 0 0 0 100vmax var(--color-ligth-100);
    background-color: var(--color-ligth-100);

    @include for-width(-mobile-md) {
      display: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: -20px;
    z-index: -1;
    width: 100%;
    height: 100%;
    max-width: 490px;
    background-image: url("../img/common.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
}

embed {
  width: 100%;

  @include for-width(-tablet) {
    height: auto;
  }
}

