.nav {
  @include flex-all-sb;

  margin-left: rem(160);
  width: 100%;
  font-size: 14px;

  &__list {
    @include flex-all-sb;

    @include for-width(-tablet) {
      flex-direction: column;
      margin-bottom: 30px;
      padding-top: rem(80);
    }

    @include for-width(-mobile-l) {
      padding-top: rem(50);
    }

    @include for-width(-mobile-sl) {
      padding-top: rem(40);
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: rem(42);

      @include for-width(-laptop-mac) {
        margin-right: rem(25);
      }

      @include for-width(-tablet) {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
  }

  &__link {
    font-size: 16px;
  }

  &.menu--active {
    transform: translateX(0);
  }

  @include for-width(-laptop-mac) {
    margin-left: rem(100);
  }

  @include for-width(-tablet) {
    position: absolute;
    right: 0;
    top: var(--header-height);
    flex-direction: column;
    overflow-x: hidden;
    overflow-y: auto;
    width: 100vw;
    height: calc(100vh - var(--header-height));
    background-color: var(--color-purple-300);
    transform: translateX(100%);
    transition: transform 0.2s linear;
  }
}
