:root {
  --font-family: "roboto", sans-serif;
  --content-width: 1440px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-white: #fff;
  --color-black: #000;
  --color-ligth-100: #eef2ff;
  --color-ligth-150: #f3f6ff;
  --color-ligth-200: #f5f7ff;
  --color-ligth-250: #e6e7f2;
  --color-gray-300: #9fa7b4;
  --color-gray-400: #42424e;
  --color-dark-400: #2c2c34;
  --color-purple-200: #a7b9f1;
  --color-purple-300: #6366f1;
  --color-purple-400: #4a4eee;
  --color-purple-800: #2d006d;
  --color-linear-400: linear-gradient(180deg, #b9c0ff 0%, #6270f4 100%);
  --color-linear-500: linear-gradient(180deg, #b9ffb9 0%, #6270f4 100%);
  --color-linear-600: linear-gradient(89.74deg, #302383 0.25%, #5167dc 99.79%);
  --color-linear-700: linear-gradient(89.74deg, #243565 0.25%, #6366f1 99.79%);
  --radius-main: 3px;
  --shadow-main: 0 4px 4px rgba(0, 0, 0, 0.1);
}

