  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 85px;
  transform: translateY(-3px);
}

.section-background {
  position: relative;
  clip-path: inset(0 -100vmax);
  box-shadow: 0 0 0 100vmax var(--color-ligth-200);
  background-color: var(--color-ligth-200);
}
.section-background::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  clip-path: inset(0 -17% 0 -100vmax);
  width: 20%;
  height: 100%;
  min-width: 325px;
  box-shadow: 0 0 0 100vmax var(--color-ligth-100);
  background-color: var(--color-ligth-100);
}
