.form {
  padding: rem(60);
  background-color: var(--color-white);

  &__title {
    margin-bottom: rem(38);
    font-weight: 700;
    font-size: 23px;
    line-height: 1.75;

    @include for-width(-tablet) {
      text-align: center;
    }
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: rem(32);
    gap: rem(40) rem(20);

    @include for-width(-mobile-sl) {
      grid-template-columns: 1fr;
      gap: rem(30);
    }
  }

  &__area {
    position: relative;
    width: 100%;
  }

  &__label {
    display: block;
    margin-bottom: 11px;
    font-weight: 400;
    font-size: 13px;
    line-height: 1.7;
    color: var(--color-gray-400);
  }

  &__input {
    border: 1px solid var(--color-gray-300);
    border-radius: var(--radius-main);
    padding: 10px;
    width: 100%;
    font-size: 15px;
    line-height: 1.5;
  }

  &__message {
    position: relative;
    display: flex;
    flex-direction: column;
    grid-column: 1 / span 2;

    textarea {
      height: 85px;
      max-width: none;
      resize: none;
    }

    @include for-width(-mobile-sl) {
      grid-column: auto;
    }
  }

  &__btn {
    padding: 16px;
    max-width: none;
    font-weight: 700;
    font-size: 17px;
    line-height: 1.3;
  }

  &__status {
    position: absolute;
    left: 5px;
    top: 103%;
    font-size: 12px;
    text-transform: capitalize;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s linear, visibility 0.2s linear;

    &--error {
      color: #f00;
    }

    &--succes {
      color: #008000;
    }
  }

  @include for-width(-laptop-mac) {
    padding: rem(40);
  }

  @include for-width(-mobile-md) {
    padding: rem(30) rem(20);
  }
}

.status {
  .form__status {
    opacity: 1;
    visibility: visible;
  }
}
