.header {
  position: fixed;
  top: 0;
  z-index: 10;
  padding: 20px 0;
  width: 100%;
  color: var(--color-white);
  background-color: var(--black-color);
  transition-property: background-color, top;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;

  &__inner {
    @include flex-all-sb;
  }

  &__actions {
    @include flex;

    margin-left: auto;
    width: 100%;
    max-width: 356px;

    .btn {
      &:not(:last-child) {
        margin-right: rem(30);
      }

      @include for-width(-laptop-mac) {
        max-width: 130px;
      }
    }

    @include for-width(-laptop-mac) {
      max-width: 290px;
    }

    @include for-width(-tablet) {
      margin: 0 auto rem(40);
    }
  }

  &.hide {
    top: calc(var(--header-height) * -1);

    &.header--active {
      top: 0;
    }
  }

  &.header-scroll {
    background: var(--color-linear-600);
  }

}

.burger {
  @include burger;

  margin-left: 20px;

  @include for-width(+tablet) {
    display: none;
  }
}
