.simple {
  &__hero {
    @include flex-all-center;

    padding: calc(var(--header-height) + rem(50)) 0 rem(80);
    flex-direction: column;
    text-align: center;
    color: var(--color-white);
    background: var(--color-linear-700);

    >* {
      margin: 0 auto;
      max-width: 650px;
      text-align: center;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 38px;
    line-height: 1.6;
  }

  &__text {
    margin-top: rem(20);
  }

  &__content {
    .section {
      &__title {
        &::before {
          max-width: 600px;
        }
      }

      &__content {
        margin-right: 20px;
        max-width: 740px;

        @include for-width(-tablet) {
          margin-right: auto;
          margin-bottom: 30px;
        }
      }

      &__image {
        width: 100%;
      }
    }

    &--form {
      padding-top: 35px;

      @include for-width(-tablet) {
        padding-top: 0;
      }
    }
  }

  &__contact {
    @include flex-all-sb;

    clip-path: inset(0 -100vmax);
    padding: rem(160) 0;
    box-shadow: 0 0 0 100vmax var(--color-ligth-200);
    background-color: var(--color-ligth-200);

    @include for-width(-tablet) {
      flex-direction: column;
      padding: rem(100) 0;
    }
  }

  &__contact-content {
    margin: 0 auto;
  }

  &__contacts {
    @include for-width(-tablet) {
      display: flex;
      justify-content: center;
      margin: 0 auto rem(50);
    }

    @include for-width(-mobile-l) {
      flex-direction: column;
      justify-content: center;
      width: 100%;
      max-width: 450px;
    }
  }

  &__contact-form {
    margin-left: 30px;
    width: 100%;
    max-width: 600px;

    @include for-width(-tablet) {
      margin-left: 0;
      max-width: none;
    }
  }

  &__contact-title {
    margin-bottom: rem(70);
    font-weight: 700;
    font-size: 38px;
    line-height: 1.6;

    @include for-width(-tablet) {
      text-align: center;
    }

    @include for-width(-mobile-md) {
      margin-bottom: rem(30);
    }
  }

  &__contacts-item {
    position: relative;
    border-radius: 12px;
    padding-left: rem(100);
    line-height: 2;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 12px;
      width: 60px;
      height: 60px;
      background-color: var(--color-white);
    }

    &::after {
      content: "";
      position: absolute;
      mask-position: center;
      mask-repeat: no-repeat;
      mask-size: contain;
      background-color: var(--color-purple-300);
      transform: translate(50%, -50%);
    }

    &:not(:last-child) {
      margin-bottom: rem(65);

      @include for-width(-tablet) {
        margin-right: rem(30);
        margin-bottom: 0;
      }

      @include for-width(-mobile-l) {
        margin-right: 0;
        margin-bottom: rem(30);
      }
    }

    a {
      display: inline-block;
      font-weight: 700;
      font-size: 17px;
    }

    p {
      font-weight: 400;
      font-size: 11px;
    }

    &--mail {
      &::after {
        left: 4px;
        top: 31px;
        mask-image: url("../img/sprite.svg#plane");
        width: 25px;
        height: 23px;
      }
    }

    &--phone {
      &::after {
        left: 2px;
        top: 30px;
        mask-image: url("../img/sprite.svg#phone");
        width: 28px;
        height: 25px;
      }
    }

    @include for-width(-mobile-sl) {
      padding-left: rem(80);
    }
  }
}
